import vue from '@/main.js'


const commoneUrl = "http://120.76.132.152:8091/wxapi"; // 

//post
function postRequest(url, data) {
	var promise = new Promise((resolve, reject) => {
		//data 
		if (!data) {
			data = {}
		}
		var postData = data;
		// if (uni.getStorageSync('userTel')) {
		// 	//()  
		// 	// postData['userid'] = uni.getStorageSync('userinfo').id
		// }
		uni.request({
			url: commoneUrl + url,
			data: postData,
			method: 'POST',
			// dataType:"jsonp",
			// jsonp:"callbackparam",//jsonp
			// jsonpCallback:"success_jsonpCallback",
			header: {
				'content-type': 'application/json;charset=UTF-8',
				'Authorization': uni.getStorageSync('token')
				//token
			},
			success: function(res) {
				if (res.data.code === 401) {
					uni.removeStorageSync('token')
					uni.redirectTo({
						url: '/pages/login/login'
					});
					return
				}
				resolve(res.data);
			},
			fail: function(e) {
				reject('');
			}
		})
	});
	return promise;
}

//get
function getRequest(url, data) {
	var promise = new Promise((resolve, reject) => {
		uni.request({
			url: commoneUrl + url,
			data: data,
			method: "GET",
			dataType: 'json',
			header: {
				'content-type': 'application/json',
				'Authorization': uni.getStorageSync('token')
				// 'user-token': uni.getStorageSync('userinfo').token
			},
			success: function(res) {
				if (res.data.code === 401 || res.data
					.code === 403) {
					uni.removeStorageSync('token')
					uni.redirectTo({
						url: '/pages/login/login'
					});
					return
				}
				resolve(res.data);
			},
			fail: function(e) {
				reject('');
			},
		});
	});
	return promise;
}
